import { select, event } from "d3";
import { Context } from "./context";
import { Helpers } from "./type/helpters";

/**
 * 图表事件管理对象
 * @since 2.0
 */
export class Events {
  private ctx: Context;

  private helpers: Helpers;

  public constructor(ctx: Context, helpers: Helpers) {
    this.ctx = ctx;
    this.helpers = helpers;
    this.init();
  }

  /**
   * 事件初始化
   */
  private init() {
    this.initContainerEvents();
    this.initPlotEvents();
  }

  /**
   * HTML 根元素事件
   * @since 2.0
   */
  private initContainerEvents() {
    // 根元素鼠标事件
    select(this.ctx.container)
      .on("mousemove touchmove", () => {
        event.preventDefault();
        this.ctx.emit("container.mouse.move", event.layerX, event.layerY);
      })
      .on("mouseleave touchend", () => {
        this.ctx.emit("container.mouse.left", event.layerX, event.layerY);
      })
      .on("mousedown", () => {
        this.ctx.emit("container.mouse.down", event.layerX, event.layerY);
      })
      .on("mouseup", () => {
        this.ctx.emit("container.mouse.up", event.layerX, event.layerY);
      })
      .on("click", () => {
        event.preventDefault();
        this.ctx.emit("container.mouse.click", event.layerX, event.layerY);
      });
  }

  /**
   * 初始化鼠标事件
   *
   * @since 2.0
   */
  private initPlotEvents() {
    // 监听鼠标移动事件
    this.ctx.on("container.mouse.move", (x: number, y: number) => {
      // 如果 hover 到了非 svg 元素上需要忽略
      if (typeof event.target.className === "string") return;

      const plotX = Number(this.ctx.plotBackgound.attr("x"));
      const plotY = Number(this.ctx.plotBackgound.attr("y"));
      const plotWidth = Number(this.ctx.plotBackgound.attr("width"));
      const plotHeight = Number(this.ctx.plotBackgound.attr("height"));
      const outBound = y > plotY + plotHeight || x > plotX + plotWidth;

      if (outBound) {
        this.ctx.emit("plot.mouse.left");
      } else {
        this.ctx.emit("plot.mouse.move", x, y);
      }
    });
    // 监听鼠标离开事件
    this.ctx.on("container.mouse.left", () => {
      this.ctx.emit("plot.mouse.left");
    });
    // 监听鼠标点击事件
    this.ctx.on("container.mouse.click", (x: number, y: number) => {
      const plotX = Number(this.ctx.plotBackgound.attr("x"));
      const plotY = Number(this.ctx.plotBackgound.attr("y"));
      const plotWidth = Number(this.ctx.plotBackgound.attr("width"));
      const plotHeight = Number(this.ctx.plotBackgound.attr("height"));
      const outBound = y > plotY + plotHeight || x > plotX + plotWidth;
      if (!outBound) {
        this.ctx.emit("plot.mouse.click", x, y);
      }
    });
  }
}
