import { Selection, ScaleOrdinal, select, event } from "d3";
import { Context } from "../context";
import { LegendData, ShapeType } from "../type/data";
import { nodeWidth, nodeHeight } from "../lib/svg-helper";
import { LegendOptions } from "../type/options";
import { DataModel } from "../data-model";

export class Legend {
  /**
   * 过滤的数据系列名
   * @since 2.0
   */
  public disabledKeys: string[] = [];

  /**
   * 全局上下文
   * @since 2.0
   */
  private ctx: Context;

  /**
   * 图例对象
   * @since 2.0
   */
  private group: Selection<SVGGElement, {}, HTMLElement, {}>;

  /**
   * 图例项对象
   * @since 2.0
   */
  private itemGroup: Selection<SVGGElement, {}, HTMLElement, {}>;

  /**
   * 图例盒子对象
   * @since 2.0
   */
  private box: Selection<SVGRectElement, {}, HTMLElement, {}>;

  /**
   * 整个图例的高度
   * @since 2.0
   */
  public height: number = 0;

  /**
   * 整个图例区的宽度
   * @since 2.0
   */
  public width: number = 0;

  public constructor(ctx: Context) {
    this.ctx = ctx;
    const { classPrefix, root } = this.ctx;
    this.group = root
      .append("g")
      .attr("class", `${classPrefix}-chart-legend`)
      .attr("data-z-index", "7");
    this.box = this.group
      .append("rect")
      .attr("class", `${classPrefix}-chart-legend-box`)
      .attr("rx", "0")
      .attr("ry", "0")
      .attr("x", "0")
      .attr("y", "0")
      .attr("visibility", "visible")
      .attr("fill", "none");
    this.itemGroup = this.group.append("g").attr("data-z-index", "1");
  }

  /**
   *
   * @param serieScale 数据系列比例尺
   * @param valueKey
   */
  public update(
    serieScale: ScaleOrdinal<string, string>,
    valueKey: string,
    type: ShapeType,
    model: DataModel
  ) {
    const enable = this.ctx.getOption("legend.enable", true);
    if (enable) {
      const alias = this.ctx.getOption(`scale.${valueKey}.alias`);
      const legendData: LegendData[] = [];
      const domain = serieScale.domain();
      for (let i = 0; i < domain.length; i++) {
        const col = domain[i];
        legendData.push({
          label: alias || (valueKey === "default" ? valueKey : col),
          color: serieScale(col),
          key: col,
          type,
          index: i,
          values: model.getDataFromSerieName(col),
        });
      }
      this.draw(legendData);
    } else {
      const { classPrefix } = this.ctx;
      this.height = 0;
      this.itemGroup.selectAll(`.${classPrefix}-chart-legend-item`).remove();
    }
  }

  private draw(legendData: LegendData[]) {
    const { classPrefix, theme } = this.ctx;
    const { text } = theme.legend;
    const chartWidth = parseInt(this.ctx.background.attr("width"), 10);
    const { formatter } = this.ctx.getOption("legend", {});
    const cls = `${classPrefix}-chart-legend-item`;

    // 清除所有的图例项
    this.itemGroup.selectAll(`.${cls}`).remove();

    const items = this.itemGroup.selectAll(`.${cls}`).data(legendData);

    let maxTextWidth = 0;
    const enter = items
      .enter()
      .append("g")
      .attr("class", (d: LegendData) => {
        return `${cls} ${classPrefix}-chart-${d.type}-series`;
      })
      .call(g => {
        // 图例的色块
        g.each(function node(d, i) {
          const legendItemGroup = select(this).style("cursor", "pointer");
          if (d.type === "line") {
            legendItemGroup
              .append("path")
              .attr("d", `M 0 11 L 16 11`)
              .attr("fill", "none")
              .attr("stroke", d.color)
              .attr("stroke-width", "2");
          }

          if (d.type === "pie") {
            legendItemGroup
              .append("rect")
              .attr("width", 12)
              .attr("height", 12)
              .attr("rx", 6)
              .attr("ry", 6)
              .attr("fill", d.color)
              .attr("y", 5);
          }

          legendItemGroup
            .append("text")
            .attr("x", 21)
            .attr("y", 15)
            .attr("text-anchor", "start")
            .attr("data-z-index", "2")
            .style("font-size", `${text.fontSize}px`)
            .style("color", text.color)
            .style("fill", text.color)
            .append("tspan")
            .text(() => {
              let text = d.label;
              if (formatter) {
                text = formatter(
                  d.label,
                  i,
                  legendData.length - 1 === i,
                  legendData
                );
              }
              return text;
            });

          const textWidth = nodeWidth(legendItemGroup.node());
          maxTextWidth = textWidth > maxTextWidth ? textWidth : maxTextWidth;
        });
      });

    let xOffset = 8; // 每个图例项的 X 偏移量
    let yOffset = 3; // 每个图例项的 Y 偏移量
    const align = this.ctx.getOption("legend.align");
    items
      .merge(enter)
      .attr("transform", function setItemOffset() {
        if (align === "bottom") {
          const x = xOffset;
          xOffset += maxTextWidth + 20;
          if (xOffset > chartWidth) {
            xOffset = maxTextWidth;
            yOffset += text.fontSize * 2;
            return `translate(0, ${yOffset})`;
          }
          return `translate(${x}, ${yOffset})`;
        }
        const x = xOffset;
        const y = yOffset;
        yOffset += text.fontSize * 2;
        return `translate(${x}, ${y})`;
      })
      .on("mouseenter", this.onLegendItemHover.bind(this))
      .on("mouseleave", this.onLegendItemLeft.bind(this))
      .on("click", this.onLegendItemClick.bind(this))
      .call(g => {
        g.selectAll("path").attr("fill", (d: LegendData) =>
          this.disabledKeys.find(k => k === d.key)
            ? text.disabledColor
            : d.color
        );
        g.selectAll("text").style("fill", (d: LegendData) =>
          this.disabledKeys.find(k => k === d.key)
            ? text.disabledColor
            : text.color
        );
      });

    this.width = Math.round(nodeWidth(this.itemGroup.node()) + 16);
    this.height = Math.round(nodeHeight(this.itemGroup.node()) + 6);
    this.box.attr("width", this.width);
    this.box.attr("height", this.height);

    this.setPosition();
  }

  /**
   * 更新图例区的位置
   * @since 2.0
   */
  private setPosition() {
    const { align } = this.ctx.getOption("legend") as LegendOptions;
    const { bottom, right, top } = this.ctx.theme.padding;
    const chartWidth = parseInt(this.ctx.background.attr("width"), 10);
    const chartHeight = parseInt(this.ctx.background.attr("height"), 10);
    let x: number;
    let y: number;
    if (align === "bottom") {
      x = Math.floor(chartWidth / 2) - Math.floor(this.width / 2);
      y = chartHeight - bottom - this.height;
    } else if (align === "right") {
      x = chartWidth - right - this.width;
      y =
        Math.floor((chartHeight - top - bottom) / 2) -
        Math.floor(this.height / 2);
    }

    this.group.attr("transform", `translate(${x}, ${y})`);
  }

  private onLegendItemHover(legendData: LegendData) {
    // 被禁用的元素不需要触发 Hover 事件
    if (this.disabledKeys.includes(legendData.key)) return;
    this.ctx.emit("legend.hover", legendData);
  }

  private onLegendItemLeft(legendData: LegendData) {
    this.ctx.emit("legend.left", legendData);
  }

  private onLegendItemClick(legendData: LegendData) {
    event.stopPropagation();
    const { key } = legendData;
    const disabled = this.disabledKeys.findIndex(k => k === key) !== -1;
    const data = this.itemGroup
      .selectAll(`.${this.ctx.classPrefix}-chart-legend-item`)
      .data() as LegendData[];
    const index = data.findIndex(d => d.key === key);
    if (disabled) {
      this.disabledKeys = this.disabledKeys.filter(k => k !== key);
    } else {
      this.disabledKeys.push(key);
    }
    this.draw(data);
    this.ctx.emit("legend.clicked", data[index], !disabled);
    this.onLegendItemLeft(legendData);
  }
}
