import { Selection } from "d3";
import { Context } from "../context";

/**
 * 注释
 */
export interface AnnotationBaseOptions {
  /**
   * 元素样式
   */
  textStyle?: string;

  /**
   * x 偏移量
   */
  offsetX?: number;

  /**
   * y 偏移量
   */
  offsetY?: number;
}

/**
 *  Annotation 位置相关属性的类型定义
 */
export type AnnotationPosition = [string | number, string | number];

export interface AnnotationLabelOptions extends AnnotationBaseOptions {
  /**
   * 文本内容
   */
  content?: string | number;

  /**
   * 位置
   */
  position?: AnnotationPosition;
}

export class Annotation {
  private ctx: Context;

  private group: Selection<SVGGElement, {}, HTMLElement, {}>;

  private labels: Selection<SVGGElement, {}, HTMLElement, {}>;

  private shapes: Selection<SVGGElement, {}, HTMLElement, {}>;

  private labelOptions: { [key: string]: AnnotationLabelOptions } = {};

  public constructor(ctx: Context) {
    this.ctx = ctx;
    const { classPrefix, root } = this.ctx;
    this.group = root
      .append("g")
      .attr("class", `${classPrefix}-chart-annotation`)
      .attr("data-z-index", "6")
      .attr("transform", "translate(0, 0)");

    this.labels = this.group
      .append("g")
      .attr("class", `${classPrefix}-chart-annotation-labels`)
      .attr("transform", "translate(0, 0)");

    this.shapes = this.group
      .append("g")
      .attr("class", `${classPrefix}-chart-annotation-shapes`)
      .attr("transform", "translate(0, 0)");
  }

  public label(key: string, cfg: AnnotationLabelOptions) {
    this.labelOptions[key] = cfg;
    return this;
  }

  private drawTimer: NodeJS.Timeout;

  public draw() {
    clearTimeout(this.drawTimer);
    this.drawTimer = setTimeout(() => {
      this.labels.selectAll("g").remove();
      Object.values(this.labelOptions).forEach(cfg => {
        this.drawLabels(cfg);
      });
    }, 10);
  }

  public drawLabels(cfg: AnnotationLabelOptions) {
    const { classPrefix } = this.ctx;
    const label = this.labels
      .append("g")
      .attr(
        "class",
        `${classPrefix}-chart-label ${classPrefix}-chart-annotation-label`
      );
    const { position, content, offsetX, offsetY, textStyle } = cfg;
    const [x = 0, y = 0] = position || [];

    label
      .append("text")
      .attr("text-anchor", "middle")
      .attr("style", replaceSVGStyleToHTML(textStyle))
      .attr("transform", () => {
        const width = this.ctx.getPlotWidth();
        const height = this.ctx.getPlotHeight();
        const xPos = typeof x === "number" ? x : (parseFloat(x) / 100) * width;
        const yPos = typeof y === "number" ? y : (parseFloat(y) / 100) * height;
        return `translate(${xPos},${yPos})`;
      })
      .attr("dx", offsetX)
      .attr("dy", offsetY)
      .append("tspan")
      .text(content);
  }
}

function replaceSVGStyleToHTML(style: string) {
  return style
    .replace(/color:/, "fill:")
    .replace(/background-color:/, "stroke");
}
