import {
  pie,
  arc,
  event,
  select,
  PieArcDatum,
  scaleOrdinal,
  format,
  Arc,
  line,
} from "d3";
import { Shape } from "./shape";
import { Color, PolarProps } from "../type/chart";
import { PolarCoordinate } from "../coordinates/polar-coord";
import { Context } from "../context";
import { Helpers } from "../type/helpters";
import { LegendData } from "../type/data";

export class Pie extends Shape {
  private radius: number = 0;

  private innerRadius: number = 0;

  private arcInstance: Arc<any, PieArcDatum<string>>;

  private hoverArcInstance: Arc<any, PieArcDatum<string>>;

  public constructor(ctx: Context, coord: PolarCoordinate, helpers: Helpers) {
    super(ctx, coord, helpers);
    const { width, height } = this.coordinate;
    this.radius = Math.min(width, height) / 2;
    this.ctx.on("legend.hover", this.onLegendItemHover, this);
    this.ctx.on("legend.left", this.onLegendItemLeft, this);
    this.ctx.on("legend.clicked", this.onLegendClick, this);
  }

  /**
   * 开始监听事件
   */
  private start() {
    if ("ontouchstart" in document) {
      this.seriesGroup
        .selectAll(`.${this.ctx.classPrefix}-chart-series`)
        .selectAll("path")
        .style("-webkit-tap-highlight-color", "transparent")
        .on("touchmove", this.showHover.bind(this))
        .on("touchend", this.hideHover.bind(this))
        .on("click", this.onClick.bind(this));
    } else {
      this.seriesGroup
        .selectAll(`.${this.ctx.classPrefix}-chart-series`)
        .selectAll("path")
        .on("mousemove", this.showHover.bind(this))
        .on("mouseleave", this.hideHover.bind(this))
        .on("click", this.onClick.bind(this));
    }
  }

  public position(pos: string) {
    this.model.source(this.ctx.dataSource);
    if (typeof pos === "string") {
      this.valueKey = pos;
    }
    return this;
  }

  public color(color: string | Color) {
    if (typeof color === "string") {
      this.serieKey = color;
    } else if (typeof color === "object" && color !== null && color.key) {
      const { key, formatter, colors } = color;
      this.serieKey = key;
      if (formatter || colors) {
        this.ctx.theme.color = formatter || colors;
      } else {
        this.ctx.theme.color = null;
      }
    } else {
      this.serieKey = null;
    }
    this.colorKey = this.serieKey;
    return this;
  }

  public coord(cfg: PolarProps["coord"]) {
    const { innerRadius } = cfg;
    // 如果 innerRadius 是小于 1 的数字或百分比的字符串，代表是百分比系数，否则则认为是具体的数值
    if (typeof innerRadius === "number") {
      this.innerRadius = innerRadius;
    }
    return this;
  }

  public updateModel() {
    this.model.define("default", this.valueKey, this.serieKey, this.colorKey);
  }

  public updateColorScale() {
    let colorDomain = this.model.getKeyNames(this.colorKey);

    // series 需要按照值大小排序
    colorDomain = colorDomain.sort((a, b) => {
      const prev = this.model.get("default", a);
      const next = this.model.get("default", b);
      return next - prev;
    });

    // 设置每个系列的颜色
    const { color, defaultColor } = this.ctx.theme;
    let colorRange: string[] = [];
    if (typeof color === "function") {
      for (let i = 0; i < colorDomain.length; i++) {
        colorRange.push(color(colorDomain[i], i, i === colorDomain.length - 1));
      }
    } else if (Array.isArray(color)) {
      colorRange = color;
    } else {
      colorRange = defaultColor;
    }
    this.colorScale = scaleOrdinal<string>()
      .domain(colorDomain)
      .range(colorRange);
  }

  public draw(updateModel = true) {
    if (updateModel) this.updateModel();
    this.updateColorScale();

    // 先更新图例的位置
    this.helpers.legend.update(
      this.colorScale,
      this.valueKey,
      "pie",
      this.model
    );

    (this.coordinate as PolarCoordinate).draw();

    this.drawSeries();

    this.start();
  }

  public drawSeries() {
    const enable = this.ctx.getOption("interaction.element-select");
    const { width, height } = this.coordinate;
    this.radius = Math.min(width, height) / 2;

    // series 需要按照值大小排序
    const seriesDomain = this.model.getSerieNames().sort((a, b) => {
      const prev = this.model.get("default", a);
      const next = this.model.get("default", b);
      return next - prev;
    });

    const pieInstance = pie<string>()
      .value(col => {
        return this.model.get("default", col);
      })
      .sort(null);
    const buildData = pieInstance(seriesDomain);

    this.seriesGroup.selectAll("g").remove();

    this.seriesGroup
      .attr("transform", `translate(${width / 2},${height / 2})`)
      .append("g")
      .attr(
        "class",
        `${this.ctx.classPrefix}-chart-series ${this.ctx.classPrefix}-chart-pie-series`
      )
      .call(g => {
        this.arcInstance = arc<PieArcDatum<string>>()
          .innerRadius(
            this.innerRadius < 1
              ? this.radius * this.innerRadius
              : this.innerRadius
          )
          .outerRadius(this.radius * 0.82);
        g.selectAll("path")
          .data(buildData)
          .join("path")
          .style("cursor", enable ? "pointer" : "normal")
          .transition()
          .duration(500)
          .attr(
            "class",
            d => `${this.ctx.classPrefix}-chart-point color-${d.index}`
          )
          .attr("d", this.arcInstance)
          .attr("fill", d => this.colorScale(d.data));
      });

    // 绘制 Hover 图形
    this.hoverArcInstance = arc<PieArcDatum<string>>()
      .innerRadius(this.radius * 0.82)
      .outerRadius(this.radius * 0.9);
    this.seriesGroup
      .append("g")
      .attr(
        "class",
        `${this.ctx.classPrefix}-chart-series ${this.ctx.classPrefix}-chart-pie-series`
      )
      .call(g => {
        g.selectAll("path")
          .data(buildData)
          .join("path")
          .attr(
            "class",
            d => `${this.ctx.classPrefix}-chart-halo color-${d.index}`
          )
          .attr("data-index", d => d.index)
          .attr("d", this.hoverArcInstance)
          .attr("fill", d => this.colorScale(d.data))
          .style("opacity", "0");
      });

    this.drawDataLabels(buildData);
    return this;
  }

  public drawDataLabels(data: PieArcDatum<string>[]) {
    const { enable, formatter } = this.options.dataLabels;
    const labelArcInstance = arc<PieArcDatum<string>>()
      .innerRadius(this.radius * 0.9)
      .outerRadius(this.radius * 0.9);

    const { root, classPrefix } = this.ctx;
    const labelGroup = root
      .selectAll(`.${classPrefix}-chart-data-labels`)
      .data(data)
      .join("g")
      .attr(
        "class",
        (_, i) =>
          `${classPrefix}-chart-data-labels ${classPrefix}-chart-series-${i}`
      )
      .attr("data-z-index", 6)
      .attr("transform", this.seriesGroup.attr("transform"));

    labelGroup.selectAll("g").remove();
    labelGroup.selectAll("path").remove();

    if (!enable) return;

    const midAngle = (d: PieArcDatum<string>) => {
      return d.startAngle + (d.endAngle - d.startAngle) / 2;
    };

    const labels = labelGroup
      .append("g")
      .attr("class", `${classPrefix}-chart-label ${classPrefix}-data-label`)
      .attr("data-z-index", 1)
      .attr("transform", d => {
        const pos = labelArcInstance.centroid(d);
        pos[0] = this.radius * (midAngle(d) < Math.PI ? 1 : -1);
        return `translate(${pos.join(",")})`;
      });

    labels
      .append("text")
      .attr("data-z-index", 1)
      .attr("dy", "0.35em")
      .style("text-anchor", d => {
        return midAngle(d) < Math.PI ? "start" : "end";
      })
      .style("font-size", "11px")
      .style("font-weight", "bold")
      .text((d, i) => {
        const percentage = getPercentage(d.startAngle, d.endAngle);
        if (typeof formatter === "function") {
          return formatter(d.value, i, {
            value: d.value,
            percent: getPercentage(d.startAngle, d.endAngle),
            serieName: d.data,
            colorName: this.colorScale(d.data),
          });
        }
        return `${d.value} (${percentage}%)`;
      });

    labelGroup
      .append("path")
      .attr("class", `${classPrefix}-data-connector`)
      .attr("d", d => {
        const pos = labelArcInstance.centroid(d);
        pos[0] = this.radius * 0.95 * (midAngle(d) < Math.PI ? 1 : -1);
        return line()([
          this.arcInstance.centroid(d),
          labelArcInstance.centroid(d),
          pos,
        ]);
      })
      .attr("fill", "none")
      .attr("stroke", d => {
        return this.colorScale(d.data);
      })
      .attr("stroke-width", 1);
  }

  private onClick({ data, index, value, endAngle, startAngle }) {
    event.preventDefault();
    const enable = this.ctx.getOption("interaction.element-select");
    if (enable) {
      this.drawSelected(index);
      const x = event.layerX;
      const y = event.layerY;
      const { valueKey, model, colorScale, serieKey } = this;
      const alias = this.ctx.getOption(`scale.${valueKey}.alias`);
      const label = alias || data.key === "default" ? valueKey : data;
      this.ctx.emit("interaction.selected", {
        x,
        y,
        xPos: x,
        index,
        metaData: [
          {
            value,
            title: label,
            color: colorScale(data),
            percentage: getPercentage(startAngle, endAngle),
            row: model
              .getDataFromCategoryName("default")
              .find(d => d[serieKey] === data),
          },
        ],
      });
    }
  }

  private drawSelected(index: number) {
    this.seriesGroup
      .selectAll(`.${this.ctx.classPrefix}-chart-point`)
      .transition()
      .duration(500)
      .attr("d", this.arcInstance);
    const arcInstance = arc<PieArcDatum<string>>()
      .innerRadius(
        this.innerRadius < 1 ? this.radius * this.innerRadius : this.innerRadius
      )
      .outerRadius(this.radius);
    this.seriesGroup
      .select(`.${this.ctx.classPrefix}-chart-point.color-${index}`)
      .transition()
      .duration(500)
      .attr("d", arcInstance);
  }

  /**
   * 显示辅助点
   */
  private showHover({ data, index, value, endAngle, startAngle }) {
    this.seriesGroup
      .selectAll(`.${this.ctx.classPrefix}-chart-series`)
      .selectAll(`.${this.ctx.classPrefix}-chart-halo`)
      .style("opacity", 0);
    this.seriesGroup
      .selectAll(`.${this.ctx.classPrefix}-chart-series`)
      .select(`.${this.ctx.classPrefix}-chart-halo[data-index="${index}"]`)
      .style("opacity", 0.3);

    event.preventDefault();
    const x = event.layerX;
    const y = event.layerY;
    const { valueKey, model, colorScale, serieKey } = this;
    const alias = this.ctx.getOption(`scale.${valueKey}.alias`);
    const label = alias || data.key === "default" ? valueKey : data;
    const dataLeaf = model
      .getDataFromCategoryName("default")
      .find(d => d[serieKey] === data);
    const metaData = [
      {
        value,
        valueText: typeof value === "number" ? format(",")(value) : "-",
        title: label,
        color: colorScale(data),
        percentage: getPercentage(startAngle, endAngle),
        row: dataLeaf ? dataLeaf.origin : null,
      },
    ];
    this.helpers.tooltip.show({
      x,
      y,
      category: undefined,
      index,
      metaData,
    });

    if (this.ctx.getOption("interaction.element-hover")) {
      if (this.currentHoverIndex !== index) {
        this.ctx.emit("interaction.hover", metaData);
        this.currentHoverIndex = index;
      }
    }
  }

  /**
   * 隐藏辅助点
   */
  private hideHover() {
    this.seriesGroup
      .selectAll(`.${this.ctx.classPrefix}-chart-halo`)
      .style("opacity", "0");
    this.helpers.tooltip.hide();
    this.currentHoverIndex = null;
  }

  private onLegendItemHover({ key }) {
    this.seriesGroup
      .selectAll(`.${this.ctx.classPrefix}-chart-series`)
      .selectAll("path")
      .call(g => {
        g.each(function highlight({ data }) {
          const ele = select(this);
          if (data === key) {
            ele.attr("opacity", 1);
          } else {
            ele.attr("opacity", 0.3);
          }
        });
      });
  }

  private onLegendItemLeft() {
    this.seriesGroup
      .selectAll(`.${this.ctx.classPrefix}-chart-series`)
      .selectAll("path")
      .call(g => {
        g.each(function highlight() {
          const ele = select(this);
          ele.attr("opacity", 1);
        });
      });
  }

  /**
   * 图例点击事件
   * @param legendData 被点击的图例信息
   * @param disabled 改图例元素是否是禁用状态
   */
  private onLegendClick(legendData: LegendData, disabled: boolean) {
    const { key } = legendData;
    if (disabled) {
      this.disabledKeys.push(key);
    } else {
      this.disabledKeys = this.disabledKeys.filter(d => d !== key);
    }
    this.model.filterColumns(this.disabledKeys);
    this.draw(false);
  }
}

function getPercentage(startAngle, endAngle, fixed = 2) {
  return parseFloat(
    (((endAngle - startAngle) / (2 * Math.PI)) * 100).toFixed(fixed)
  );
}
