import { Selection, select } from "d3";
import { Context } from "../context";
import { EventData, MetaData } from "../type/data";
import { escapeHTML } from "../lib/escape";

export class Tooltip {
  /**
   * 全局上下文
   * @since 2.0
   */
  private ctx: Context;

  /**
   * 提示框对象
   */
  private group: Selection<HTMLDivElement, {}, HTMLElement, {}>;

  public constructor(ctx: Context) {
    this.ctx = ctx;

    this.group = select(this.ctx.container)
      .append("div")
      .attr("class", `${this.ctx.classPrefix}-chart-tooltip`)
      .style("position", "absolute")
      .style("display", "none")
      .style("padding", "15px")
      .style("z-index", 99)
      .style("background", "#FFF")
      .style("white-space", "nowrap")
      .style("box-shadow", "0px 4px 8px 0px rgba(0, 0, 0, 0.15)")
      .style(
        "transition",
        "visibility 0.6s cubic-bezier(0.23, 1, 0.32, 1) 0s, left 0.6s cubic-bezier(0.23, 1, 0.32, 1) 0s, top 0.6s cubic-bezier(0.23, 1, 0.32, 1) 0s"
      );
  }

  public show(eventData: EventData) {
    const { metaData, index, x, y } = eventData;
    const { formatter } = this.ctx.getOption("tooltip");
    if (metaData.length === 0) return;
    const [{ title }] = metaData;
    let content = getTooltipContent(title, metaData);

    if (typeof formatter === "function") {
      const formatted = formatter(metaData, index);
      if (typeof formatted === "string") {
        content = formatted;
      } else {
        const [{ title }] = formatted;
        content = getTooltipContent(title, formatted);
      }
    }

    this.group
      .html(escapeHTML(content))
      .style("display", "block")
      .transition()
      .duration(60);

    const offset = 15;
    const { width, height } = window.getComputedStyle(this.group.node());
    const w = width === "auto" ? 0 : parseInt(width, 10);
    const h = height === "auto" ? 0 : parseInt(height, 10);
    const plotWidth = parseInt(this.ctx.background.attr("width"), 10);
    const plotHeight = parseInt(this.ctx.background.attr("height"), 10);
    const offsetX = parseInt(this.ctx.plotBackgound.attr("x"), 10);
    const offsetY = parseInt(this.ctx.plotBackgound.attr("y"), 10);

    // 设置水平方向位置
    if (x + w + offsetX > plotWidth) {
      this.group.style("right", `${plotWidth - x + offsetX - offset}px`);
      this.group.style("left", "unset");
    } else {
      this.group.style("right", "unset");
      this.group.style("left", `${x + offset}px`);
    }
    // 设置垂直方向位置
    if (y + h + offsetY > plotHeight) {
      this.group.style("bottom", `${plotHeight + offsetY - y}px`);
      this.group.style("top", "unset");
    } else {
      this.group.style("top", `${y + offset}px`);
      this.group.style("bottom", "unset");
    }
  }

  public hide() {
    this.group.style("display", "none");
  }
}

export function getTooltipContent(label: string, metaData: MetaData[]) {
  const title = `<h4 style="font-size:14px;color:#333;padding-bottom:10px">${label}</h4>`;
  let seriesText = "";
  for (const meta of metaData) {
    const hasLabel = !!meta.label;
    seriesText += `
    <p style="height:20px;line-height:20px;display:table-row;">
      <span style="display:table-cell;line-height:20px;vertical-align:middle;padding-right:10px;">
        <i style="display:inline-block;height:4px;width:16px;background:${
          meta.color
        };"></i>
      </span>
      ${
        hasLabel
          ? `<span style="display:table-cell;font-size:14px;color:#333;">${meta.label}</span>`
          : ""
      }
      <span style="display:table-cell;font-weight:${
        hasLabel ? 600 : 400
      };color:#333;font-size:14px;padding-left:${
      hasLabel ? "15px" : "0"
    };text-align:right;">${meta.valueText} ${
      meta.percentage ? `(${meta.percentage}%)` : ""
    }</span>
    </p>`;
  }

  return `
  ${title}
  <div style="display:table;">
    ${seriesText}
  </div>
  `;
}
