import {
  ascending,
  scaleOrdinal,
  scalePoint,
  scaleLinear,
  ScalePoint,
  select,
  line,
  format,
  Selection,
  curveCatmullRom,
} from "d3";
import moment from "moment";
import { Shape } from "./shape";
import { Color } from "../type/chart";
import { ContextOptions, Context } from "../context";
import { CategoryScaleType } from "../coordinates/coordinate";
import { RectCoordinate } from "../coordinates/rect-coord";
import { Helpers } from "../type/helpters";
import { MetaData, EventData, LegendData } from "../type/data";
import { getCategoryIndex } from "../lib/get-category-index";
import { InteractionOptions } from "../type/options";

export class Line extends Shape {
  /**
   * 分类轴数据比例尺类型
   * @since 2.0
   */
  private categoryScaleType: CategoryScaleType = "Point";

  /**
   * Crosshair
   * @since 2.0
   */
  private crosshair: Selection<SVGPathElement, {}, HTMLElement, {}>;

  public constructor(ctx: Context, coord: RectCoordinate, helpers: Helpers) {
    super(ctx, coord, helpers);
    this.crosshair = this.ctx.root
      .append("path")
      .attr("class", `${this.ctx.classPrefix}-chart-crosshair`)
      .attr("data-z-index", "2")
      .attr("stroke", "#ECECEC")
      .attr("stroke-width", 1)
      .attr("style", "pointer-events:none;")
      .attr("visibility", "hidden");

    this.ctx.on("plot.mouse.move", this.onMouseMove, this);
    this.ctx.on("plot.mouse.left", this.removeHover, this);
    this.ctx.on("plot.mouse.click", this.onClick, this);

    this.ctx.on("legend.hover", this.onLegendItemHover, this);
    this.ctx.on("legend.left", this.onLegendItemLeft, this);
    this.ctx.on("legend.clicked", this.onLegendClick, this);

    this.ctx.on("coord.zoom.zooming", this.onZoom, this);
    this.ctx.on("coord.zoom.reset", this.onZoomReset, this);
  }

  /**
   * @implements Shape
   * @param pos 数据映射关系
   * @since 2.0
   */
  public position(pos: string) {
    this.model.source(this.ctx.dataSource);
    if (typeof pos === "string") {
      [this.categoryKey, this.valueKey] = pos.split("*");
    }
    return this;
  }

  /**
   * @implements
   * @param {string} color 作为数据系列映射字段
   * @param {Color} color 以 Color 的配置来设置颜色与数据系列映射关系
   * @since 2.0
   */
  public color(color: string | Color) {
    if (typeof color === "string") {
      this.serieKey = color;
    } else if (typeof color === "object" && color !== null && color.key) {
      const { key, formatter, colors } = color;
      this.serieKey = key;
      if (formatter || colors) {
        this.ctx.theme.color = formatter || colors;
      } else {
        this.ctx.theme.color = null;
      }
    } else {
      this.serieKey = null;
    }

    this.colorKey = this.serieKey;

    return this;
  }

  public size(size: number) {
    this.shapeSize = size;
    return this;
  }

  /**
   * 设置坐标轴配置
   * @param axis 坐标轴配置
   * @since 2.0
   */
  public axis(axis: ContextOptions["axis"]) {
    if (axis.xAxis) {
      this.ctx.setOption("axis.xAxis", axis.xAxis);
    }
    if (axis.yAxis) {
      this.ctx.setOption("axis.yAxis", axis.yAxis);
    }
    return this;
  }

  /**
   * 设置图例
   * @param enable 是否显示图例
   * @param connectNullsCfg 连接空值配置
   * @since 2.0
   */
  public connectNulls(
    enable: boolean | ContextOptions["connectNulls"],
    connectNullsCfg: ContextOptions["connectNulls"] = {}
  ) {
    const cfg: ContextOptions["connectNulls"] =
      typeof enable === "boolean"
        ? { enable, ...connectNullsCfg }
        : { ...enable };
    cfg.enable = typeof cfg.enable === "boolean" ? cfg.enable : true;
    this.ctx.setOption("connectNulls", cfg);
    return this;
  }

  /**
   * @implements
   * @since 2.0
   */
  public updateModel() {
    let xSort = null;
    const { type, timeParseFormat } = this.ctx.getOption(
      `scale.${this.categoryKey}`,
      {
        type: "category",
        timeParseFormat: false,
      }
    );
    if (type === "time") {
      xSort = (a: string, b: string) => {
        return ascending(
          moment(a, timeParseFormat).valueOf(),
          moment(b, timeParseFormat).valueOf()
        );
      };
    }
    const { stack } = this.ctx.getOption(`scale.${this.valueKey}`, {
      stack: false,
    });
    this.model.define(
      this.categoryKey,
      this.valueKey,
      this.serieKey,
      this.colorKey,
      xSort,
      stack
    );
  }

  /**
   * 更新颜色比例尺
   * @implements
   * @since 2.0
   */
  public updateColorScale() {
    const colorDomain = this.model.getKeyNames(this.colorKey);
    const { color, defaultColor } = this.ctx.theme;
    let colorRange: string[] = [];
    if (typeof color === "function") {
      for (let i = 0; i < colorDomain.length; i++) {
        colorRange.push(color(colorDomain[i], i, i === colorDomain.length - 1));
      }
    } else if (Array.isArray(color)) {
      colorRange = color;
    } else {
      colorRange = defaultColor;
    }
    this.colorScale = scaleOrdinal<string>()
      .domain(colorDomain)
      .range(colorRange);
  }

  /**
   * 更新数据比例尺
   * @implements
   * @since 2.0
   */
  public updateDataScale(catDomain?: string[]) {
    const categoryDomain = catDomain || this.model.getCategoryNames();
    this.categoryScale = scalePoint().domain(categoryDomain);

    const { domain } = this.ctx.getOption(`scale.${this.valueKey}`, {
      domain: null,
    });
    const autofit = domain === "autofit";
    const valueDomain = Array.isArray(domain)
      ? domain
      : this.model.getExtent(autofit);
    this.valueScale = scaleLinear()
      .domain(valueDomain)
      .nice(5);
  }

  /**
   * 开始绘图
   * @implements
   * @since 2.0
   */
  public draw(
    updateModel = true,
    updateColorScale = true,
    updateDataScale = true
  ) {
    if (updateModel) this.updateModel();
    if (updateColorScale) this.updateColorScale();
    if (updateDataScale) this.updateDataScale();

    // 先更新图例的位置
    this.helpers.legend.update(
      this.colorScale,
      this.valueKey,
      "line",
      this.model
    );

    // 更新坐标轴
    this.coordinate.draw(
      this.valueScale,
      this.categoryScale,
      this.categoryScaleType,
      this.invert
    );

    this.drawSeries();

    const { brush, zoom }: InteractionOptions = this.ctx.getOption(
      "interaction"
    );
    this.helpers.selection.setEnable(!!brush || !!zoom);
  }

  /**
   * 绘制折线图
   * @since 2.0
   */
  public drawSeries() {
    const { classPrefix, id } = this.ctx;
    const { smooth } = this.options;
    const series = this.model.getSerieNames();
    const categories = this.model.getCategoryNames();
    const categoryScale = this.categoryScale as ScalePoint<string>;
    const serieCls = `${classPrefix}-chart-series ${classPrefix}-chart-line-series`;
    const markersCls = `${classPrefix}-chart-markers ${classPrefix}-chart-line-series`;
    const shape = this;
    this.seriesGroup.selectAll("g").remove();
    const datum = {};
    for (const serieName of series) {
      datum[serieName] = [];
      for (const cat of categories) {
        const value: number = shape.model.get(cat, serieName);
        const x: number = categoryScale(cat);
        const y: number = shape.valueScale(value);
        const color: string = shape.colorScale(serieName);
        datum[serieName].push({ value, x, y, color });
      }
    }

    this.seriesGroup
      .selectAll(`.${serieCls}`)
      .data(series)
      .join("g")
      .attr("class", (_, i) => `${serieCls} ${classPrefix}-chart-series-${i}`)
      .attr("data-z-index", (_, i) => series.length - i - 1)
      .attr("clip-path", `url(#${id})`)
      .call(g => {
        const { enable: connectNulls } = this.ctx.getOption("connectNulls");
        g.each(function serie(serieName) {
          const serieGroup = select(this);
          const data = datum[serieName];
          const defaultColor = data && data[0] && data[0].color;
          // 绘制可见折线
          serieGroup
            .append("path")
            .attr("fill", "none")
            .attr("class", `${classPrefix}-chart-shape-missing`)
            .attr("data-z-index", "1")
            .attr("stroke", defaultColor)
            .attr("stroke-width", shape.shapeSize)
            .attr("stroke-linejoin", "round")
            .attr("stroke-linecap", "round")
            .attr("d", () => {
              const lineGenerator = line<any>().defined(({ value }) =>
                connectNulls ? true : typeof value === "number"
              );
              if (smooth) {
                lineGenerator.curve(curveCatmullRom.alpha(0.3));
              }
              return lineGenerator.x(({ x }) => x).y(({ y }) => y)(
                connectNulls
                  ? data.filter(({ value }) => typeof value === "number")
                  : data
              );
            });

          // 绘制追踪线
          serieGroup
            .append("path")
            .attr("fill", "none")
            .attr("class", `${classPrefix}-chart-tracker-line`)
            .attr("data-z-index", "2")
            .attr("stroke", "rgba(192,192,192,0.0001)")
            .attr("stroke-width", "24")
            .attr("stroke-linejoin", "round")
            .attr("stroke-linecap", "round")
            .attr("d", () => {
              // @ts-ignore
              return line<any>()
                .defined(({ value }) => typeof value === "number")
                .x(({ x }) => x)
                .y(({ y }) => y)(data);
            });
        });
      });

    this.seriesGroup
      .selectAll(`.${markersCls}`)
      .data(series)
      .join("g")
      .attr("class", (_, i) => `${markersCls} ${classPrefix}-chart-series-${i}`)
      .attr("data-z-index", (_, i) => series.length - i - 1);

    const maxSerieIndex = series.length;
    for (let start = 0; start <= maxSerieIndex; start++) {
      this.seriesGroup
        .selectAll(`.${classPrefix}-chart-series-${start}`)
        .raise();
    }
  }

  private timer: NodeJS.Timeout;

  private onMouseMove(x: number, y: number) {
    const tooltipEnable = this.ctx.getOption("tooltip.enable");
    if (tooltipEnable === false) return;
    const data = this.getData(x, y);
    if (data) {
      this.helpers.tooltip.show(data);
      clearTimeout(this.timer);
      this.drawHover(data);

      if (this.ctx.getOption("interaction.element-hover")) {
        if (this.currentHoverIndex !== data.index) {
          this.ctx.emit("interaction.hover", data.metaData);
          this.currentHoverIndex = data.index;
        }
      }
    } else {
      this.removeHover();
    }
  }

  /**
   * 根据坐标获取数据
   * @since 2.0
   * @param x x 坐标
   * @param y y 坐标
   */
  public getData(x: number, y?: number): EventData {
    const scale = this.categoryScale as ScalePoint<string>;
    const domain = scale.domain();
    let index = getCategoryIndex(x, scale, "Point");
    const xValue = domain[index];
    const xPos = xValue ? scale(xValue) : x;

    if (index < 0 || Number.isNaN(index)) return null;

    if (index > domain.length - 1) {
      index = domain.length - 1;
    }

    const alias = this.ctx.getOption(`scale.${this.valueKey}.alias`);
    const metaData: MetaData[] = [];

    const values = this.model.getDataFromCategoryName(xValue);

    for (const { serieName, value, origin, colorName } of values) {
      metaData.push({
        title: xValue,
        label: alias || (serieName === "default" ? this.valueKey : serieName),
        serieName,
        valueText: typeof value === "number" ? format(",")(value) : "-",
        color: this.colorScale(colorName),
        value,
        row: origin,
      });
    }

    return {
      x: xPos,
      y,
      category: xValue,
      metaData,
      index,
    };
  }

  /**
   * 绘制标记线和标记点
   * @param eventData 当前数据点信息
   * @since 2.0
   * @todo 修复最后一个点不显示的问题
   */
  private drawHover(eventData: EventData) {
    const { x, category, metaData } = eventData;
    const offsetY = parseInt(this.ctx.plotBackgound.attr("y"), 10);
    const height = parseInt(this.ctx.plotBackgound.attr("height"), 10);

    clearTimeout(this.timer);

    this.timer = setTimeout(() => {
      // 绘制标记线
      this.crosshair
        .attr("d", `M${x},${offsetY}L${x},${height + offsetY}`)
        .attr("visibility", "visible");
      const markers = this.seriesGroup.selectAll(
        `.${this.ctx.classPrefix}-chart-markers`
      );
      markers.selectAll("circle").attr("opacity", 0);
      markers
        .selectAll("circle")
        .data(metaData)
        .join("circle")
        .attr("class", `${this.ctx.classPrefix}-chart-point`)
        .attr("r", 4)
        .attr("stroke", ({ color }) => color)
        .attr("fill", "#FFF")
        .attr("stroke-width", 2)
        .attr("opacity", 1)
        .attr("visibility", ({ value }) =>
          value === null ? "hidden" : "visible"
        )
        .attr("transform", ({ serieName }) => {
          const x = (this.categoryScale as ScalePoint<string>)(category);
          const value = this.model.get(category, serieName);
          const y = this.valueScale(value);
          return `translate(${x}, ${y})`;
        });
    }, 0);
  }

  /**
   * 隐藏标记线和标记点
   * @since 2.0
   */
  private removeHover() {
    this.crosshair.attr("visibility", "hidden");
    this.seriesGroup
      .selectAll(`.${this.ctx.classPrefix}-chart-markers circle`)
      .attr("opacity", "0");
    this.helpers.tooltip.hide();
  }

  /**
   * 监听图例事件
   * @param legendData LegendData
   */
  private onLegendItemHover({ key }) {
    this.seriesGroup
      .selectAll(`.${this.ctx.classPrefix}-chart-series`)
      .call(g => {
        g.each(function highlight(groupKey) {
          const ele = select(this);
          if (groupKey === key) {
            ele.attr("opacity", 1);
          } else {
            ele.attr("opacity", 0.1);
          }
        });
      });
  }

  /**
   * 监听图例事件
   * @param legendData LegendData
   */
  private onLegendItemLeft() {
    this.seriesGroup
      .selectAll(`.${this.ctx.classPrefix}-chart-series`)
      .call(g => {
        g.each(function highlight() {
          const ele = select(this);
          ele.attr("opacity", 1);
        });
      });
  }

  /**
   * 图例点击事件
   * @param legendData 被点击的图例信息
   * @param disabled 改图例元素是否是禁用状态
   */
  private onLegendClick(legendData: LegendData, disabled: boolean) {
    const { key } = legendData;
    if (disabled) {
      this.disabledKeys.push(key);
    } else {
      this.disabledKeys = this.disabledKeys.filter(d => d !== key);
    }
    this.model.filterColumns(this.disabledKeys);
    this.draw(false);
  }

  private onClick(x: number, y: number) {
    const mappedData = this.getData(x, y);
    if (mappedData) {
      this.ctx.emit("click", mappedData);
    }
  }

  private onZoom(range: string[]) {
    this.model.range(range);
    this.draw(false);
  }

  private onZoomReset() {
    this.draw();
  }
}
